import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {extensionSplit} from '@servicenow/library-enhanced-utils';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './_now-message.scss';

const view = (state) => {
	const {alignment} = state.properties;
	return (
		<div
			class={{
				'now-message': true,
				...extensionSplit(alignment)
			}}>
			<slot name="media" />
			<div className="now-message-child">
				<slot name="message" />
				<slot name="actions" />
			</div>
		</div>
	);
};

/**
 * Lets you compose messages using standard
 * interactions, patterns, behaviors and applied styles. Use cases include
 * empty states, boolean feedback, and confirmation messages. 
 *
 * ```jsx
 * <now-message>
 * 	<img
 * 		slot="media"
 * 		className="now-m-inline-end--xxl"
 * 		src="..."
 * 		alt="..."
 * 	/>
 * 	<div slot="message" className="now-m-block--xl now-m-inline-end--xl">
 * 		<h3 className="now-heading--lg now-m-block--0">...</h3>
 * 		<p className="now-m-block-start--lg now-m-block-end--0">...</p>
 * 	</div>
 * 	<div slot="actions" className="now-align">
 * 		<now-button
 * 			label="..."
 * 			className="now-m-inline-end--sm"
 * 		/>
 * 		<now-button label="..." />
 * 	</div>
 * </now-message>
 * ```
 *
 * @seismicElement now-message
 * @summary Message framework allowing various alignments of slotted content.
 * @uib.label Message
 * @uib.icon message
 * @uib.description A framework for presenting cohesive messages, including text, images, and actions.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties alignment
 **/
createEnhancedElement('now-message', {
	properties: {
		/**
		 * Sets the alignment. Options include: adaptive (will transition from horizontal to
		 * vertical and vice versa), vertical, vertical-centered, or horizontal.
		 * @type {('adaptive'|'vertical'|'vertical-centered'|'horizontal')}
		 * @uib.description Sets the alignment
		 */
		alignment: {
			default: 'adaptive',
			schema: {
				type: 'string',
				enum: ['adaptive', 'vertical', 'vertical-centered', 'horizontal']
			}
		}
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	slots: {
		/**
		 * Slot for media. Add illustration, tile icon, or image that brings visual
		 * impact and meaning to the message.
		 */
		media: {},
		/**
		 * Slot for message. The heading should contain a title that conveys the purpose of
		 * the component. The body should give users guidance on taking an action.
		 */
		message: {},
		/**
		 * Slot for buttons that perform actions.
		 */
		actions: {}
	},
	view
});
